@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
    font-family: 'Poppins', sans-serif;
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
body{
    background-color: #010718;
}
a{
    text-decoration: none;
    color: #010718;
}
:root{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}
.section{
    background: linear-gradient(#4070f4, #9ab0fa);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    flex-flow: column nowrap ;
    height: fit-content;
    width: 50vw;
    box-shadow: 1px 0px 30px 3px rgb(255 255 255 / 10%);
    border-radius: 10px;
    padding: 20px 0;
}
    

.that{
    color:#010718;
    border-left: #010718 7px solid;
    padding: 10px;
    background-color: #fff;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 80%;
    gap: 10px;
    height: 80px;
}
.divimg{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: #010718 2px solid;
    background-color: #fff;
}
.that .divimg img{
    width: 60%;
}
h1{
    color: #fff;
}
.that:hover{
   background-color: #010718;
}
 .section a:hover h3{
    color: #4070f4;
 }
 h3{
    color: #010718;
    font-weight: 600;
 }
 h5{
    color: #4070f4;
    font-weight: 500;
 }
 .as{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    width: 60%;
}
